.content {
  overflow: hidden;
  transition: all 1s ease-in-out;
  transform: translateZ(0);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  text-align: center;
}
.content .gm-scroll-view {
  width: calc(100% + 20px) !important;
}
.content.gm-scrollbar-container {
  width: 100%;
  height: 100vh;
}
.content-inner {
  width: 100%;
  background-color: #fff;
  min-height: 120vh;
  padding: 1.5rem;
}
.content--home {
  z-index: 2;
}
.content--about .content-inner {
  background-color: #433b8f;
}
.content--resume .content-inner {
  background-color: #f07502;
}
.content--portfolio .content-inner {
  background-color: #7cbc19;
}
.content--blog .content-inner {
  background-color: #0184cd;
}
.content--contact .content-inner {
  background-color: #c34a47;
}
@media (min-width: 1024px) {
  .content,
  .content.gm-scrollbar-container {
    left: 25%;
    width: 50%;
  }
  .content[data-position="left"],
  .content.gm-scrollbar-container[data-position="left"],
  .content.is-left,
  .content.gm-scrollbar-container.is-left {
    left: 0;
  }
  .content[data-position="right"],
  .content.gm-scrollbar-container[data-position="right"],
  .content.is-right,
  .content.gm-scrollbar-container.is-right {
    left: 50%;
  }
}
/**
 * gemini-scrollbar
 * @version 1.4.0
 * @link http://noeldelgado.github.io/gemini-scrollbar/
 * @license MIT
 */

/* disable selection while dragging */
.gm-scrollbar-disable-selection {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

/* fallback for native floating scrollbars */
.gm-prevented {
  -webkit-overflow-scrolling: touch;
}
.gm-prevented .gm-scrollbar {
  display: none;
}

/* actual gemini-scrollbar styles */
.gm-scrollbar-container {
  position: relative;
  overflow: hidden!important;
  width: 100%;
  height: 100%;
}

.gm-scrollbar {
  position: absolute;
  right: 2px;
  bottom: 2px;
  z-index: 1;
  border-radius: 3px;
}

.gm-scrollbar.-vertical {
  width: 6px;
  top: 2px;
}

.gm-scrollbar.-horizontal {
  height: 6px;
  left: 2px;
}

.gm-scrollbar .thumb {
  position: relative;
  display: block;
  width: 0;
  height: 0;
  cursor: pointer;
  border-radius: inherit;
  background-color: rgba(0,0,0,.2);
}

.gm-scrollbar .thumb:hover,
.gm-scrollbar .thumb:active {
  background-color: rgba(0,0,0,.3);
}

.gm-scrollbar.-vertical .thumb {
  width: 100%;
}

.gm-scrollbar.-horizontal .thumb {
  height: 100%;
}

.gm-scrollbar-container .gm-scroll-view {
  width: 100%;
  height: 100%;
  overflow: scroll;
  -webkit-overflow-scrolling: touch;
}

/* @option: autoshow */
.gm-scrollbar-container.gm-autoshow .gm-scrollbar {
  opacity: 0;
  transition: opacity 120ms ease-out;
}
.gm-scrollbar-container.gm-autoshow:hover .gm-scrollbar,
.gm-scrollbar-container.gm-autoshow:focus .gm-scrollbar {
  opacity: 1;
  transition: opacity 340ms ease-out;
}

.gm-resize-trigger {
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  overflow: hidden;
  pointer-events: none;
  z-index: -1;
}

html,
body {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}
body {
  margin: 0;
  background: url("../assets/images/background.jpg");
  background-size: cover;
  background-attachment: fixed;
  font-size: 15px;
}
* {
  box-sizing: border-box;
}
.portrait {
  border: 1px solid #ddd;
  padding: 1rem;
  margin: 0 auto;
  display: block;
  width: 50%;
  max-width: 20rem;
}
.btn {
  padding: 15px;
}
